// src/hooks/global.ts
function useSafeEffect(callback, dependencies, error_message) {
  useEffect(() => {
    try {
      callback();
    } catch (error) {
      console.error(error_message || "Error in useEffect:", error);
    }
  }, dependencies);
}
var useDocumentTitle = (title) => {
  useEffect(() => {
    document.title = title;
  }, [title]);
  return null;
};
var useSnapshotBulk = (configs, label) => {
  const snapshotsFirstTime = useRef([]);
  const unsubscribeFunctions = useRef([]);
  useEffect(() => {
    const start = performance.now();
    console.log(`==> ${label || "Custom snapshots"} started... `);
    const snapshotResults = configs.map((config) => snapshot(config, snapshotsFirstTime.current));
    unsubscribeFunctions.current = snapshotResults.map((result) => result.unsubscribe);
    Promise.all(snapshotResults.map((result) => result.promise)).then(() => {
      console.log(`==> ${label || "Custom snapshots"} ended. It took ${(performance.now() - start).toFixed(2)} ms`);
    });
    return () => {
      unsubscribeFunctions.current.forEach((unsubscribe) => {
        if (unsubscribe) {
          unsubscribe();
        }
      });
    };
  }, [configs, label]);
};
var useSetUserCountry = (setUserCountry, changLang) => {
  useLayoutEffect(() => {
    const currentCountry = localStorage.getItem("userCountry");
    if (!currentCountry) {
      const updateCountry = async () => {
        const country = await getUserCountryByIp();
        changLang(country === CountryOptions2.IL ? "he" : "en");
        setUserCountry(country);
        localStorage.setItem("userCountry", country);
      };
      updateCountry();
    }
  }, []);
  return null;
};

